.pokemon {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr) auto auto;
  align-items: center;

  align-self: start;

  padding: 0.5rem 1.5rem;
  margin: 0 auto 1.5rem auto;

  max-width: 600px;

  border-radius: $border-radius;
  background: #fff;
  box-shadow: 0 1px 7px rgba(0,0,0,0.15);

  &__name {
    grid-column: span 3;
    grid-row: 1; }

  &__like {
    grid-column: 6 / 7;
    grid-row: 1; }

  &__abilities {
    grid-column: 4 / 7;
    grid-row: 2 / 4;

    justify-self: start;
    align-self: start; }

  &__types {
    grid-column: 4 / 6;
    grid-row: 1;

    justify-self: end; }

  &__chart {
    grid-column: span 6;
    grid-row: 4 / 7;

    justify-self: center;
    align-self: center; }

  &__toggle-type {
    grid-column: span 6;
    grid-row: 7 / 8; }

  &__evolutions {
    grid-column: span 6;
    grid-row: 8 / 9;

    align-self: start; }

  &__sprite {
    grid-column: span 2;
    grid-row: 2 / 4;

    align-self: start; }

  @media screen and ( max-width: 700px ) {
    margin: 0 1rem 1.5rem 1rem; }

  @media screen and ( max-width: 520px ) {
    &__abilities {
      grid-column: span 6;
      grid-row: 4 / 5;

      justify-self: center;
      align-self: center; }

    &__chart {
      grid-row: 5 / 8; }

    &__toggle-type {
      grid-row: 8 / 9; }

    &__evolutions {
      grid-row: 9 / 10; }

    &__sprite {
      grid-column: span 6;
      justify-self: center; } }

  @media screen and ( max-width: 450px ) {
    grid-template-rows: auto 1fr auto auto 1.5fr auto auto;
    grid-gap: 1rem;

    &__name {
      grid-column: span 5; }

    &__abilities {
      grid-column: span 6;
      grid-row: 4 / 5;

      justify-self: center;
      align-self: center; }

    &__types {
      grid-column: span 6;
      grid-row: 3 / 4;

      justify-self: center; }

    &__chart {
      grid-row: 5 / 6; }

    &__toggle-type {
      grid-row: 6 / 7; }

    &__evolutions {
      grid-row: 7 / 8; }

    &__sprite {
      grid-row: 2 / 3;
      justify-self: center; } } }
